.ServiceLog,
.ServiceLog * {
  box-sizing: border-box;
}

.ServiceLog em {
  font-style: normal;
  font-weight: 500;
}

.ServiceLog p {
  margin: 0;
}

.ServiceLog hr {
  border: 0;
  height: 1px;
  background: #f1f1f1;
}

.ServiceLog {
  background-color: #f1f1f1;
  padding: 1em;
}

.ServiceLog-group {
  border-radius: 8px;
  background-color: #fff;
  margin-top: 1em;
  padding: 1em;
}

.ServiceLog-group:first-of-type {
  margin-top: 0;
}

.ServiceLog-header {
  overflow: hidden;
}

.ServiceLog-info {
  border: 1px solid #f1f1f1;
  border-radius: 8px;
  /* float: left; */
  padding: 0.5em 1em;
  /* width: 20%; */
  width: 100%;
}

.ServiceLog-info td {
  white-space: nowrap;
  width: 1px;
}

.ServiceLog-info td:not(:first-of-type) {
  padding-left: 0.75em;
}

.ServiceLog-info td:last-of-type {
  width: auto;
}

.ServiceLog-requests {
  /* float: left; */
  height: 200px;
  overflow-y: scroll;
  margin-top: 0.5em;
  /* width: 80%; */
}

.ServiceLog-requestButton {
  border-radius: 8px;
  border: 1px solid transparent;
  background-color: transparent;
  font-family: inherit;
  font-size: inherit;
  margin-right: 1em;
  padding: 1em;
}

.ServiceLog-requestButton.isActive {
  border-color: #e4e4e4;
}

.ServiceLog-requestButton:focus {
  border-color: #c5c5c5;
  outline: none;
}

.ServiceLog-requestHeader {
  padding: 1em;
}

.ServiceLog-nav {
  border: 1px solid #f1f1f1;
  border-radius: 8px;
  display: inline-block;
  overflow: hidden;
  transition: all ease-out 0.3s;
  width: auto;
}

.ServiceLog-nav:hover {
  border-color: #c5c5c5;
}

.ServiceLog-navButton {
  border: none;
  border-right: 1px solid #f1f1f1;
  background-color: #fff;
  color: #5e5e5e;
  font-family: inherit;
  height: 32px;
  outline: none;
  padding: 0 1em;
  transition: all ease-out 0.3s;
}

.ServiceLog-nav:hover .ServiceLog-navButton {
  border-color: #c5c5c5;
  color: #333;
}

.ServiceLog-navButton:last-of-type {
  border-right: none;
}

.ServiceLog-navButton:hover {
  background-color: #f1f1f1;
}

.ServiceLog-navButton:active {
  background-color: #e7e7e7; 
  transition: none;
}

.Service-payloads {
  overflow: hidden;
}

.Service-payloads pre {
  font-size: 85%;
  margin: 0;
}

.Service-payload {
  float: left;
  width: 50%;
}

.Service-payloadCode {
  border-radius: 8px;
  margin: 0.5em;
  overflow: hidden;
}
